import { Box, Grid, IconButton } from "@mui/material";
import GitHubIcon from "@mui/icons-material/GitHub";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import TwitterIcon from "@mui/icons-material/Twitter";
import InstagramIcon from "@mui/icons-material/Instagram";
import LightModeIcon from "@mui/icons-material/LightMode";
import BedtimeIcon from "@mui/icons-material/Bedtime";
import YouTubeIcon from "@mui/icons-material/YouTube";
import React, { useState, useRef, useEffect, useLayoutEffect } from "react";
import { useNavigate } from "react-router-dom";
import "./LayoutPage.scss";
import AboutMePage from "../AboutMe";
import MyWorkPage from "../MyWork";
import ExperiencesPage from "../Experiences";
import FeedbackPage from "../Feedback";
import ContactMePage from "../ContactMe";
import FooterMainPage from "../FooterMain";
import LoadingBar from "../component/LoadingBar/pages";

export default function LayoutPage() {
  const [lightTurnOn, setLightTurnOn] = useState(true);
  const handleLightOn = (e) => {
    setLightTurnOn(!lightTurnOn);
  };
  const [showButton, setShowButton] = useState(false);
  useEffect(() => {
    window.addEventListener("scroll", () => {
      if (window.pageYOffset > 300) {
        setShowButton(true);
      } else {
        setShowButton(false);
      }
    });
  }, []);

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    });
  };
  const aboutMeRef=useRef(null);
  const experienceRef=useRef(null);
  const contactRef=useRef(null);
  const myWorkRef=useRef(null);
  const navigateAboutMe = () => {
    aboutMeRef.current?.scrollIntoView({behavior: 'smooth'});
  };
  const navigateMyWork = () => {
    myWorkRef.current?.scrollIntoView({behavior: 'smooth'});
  };
  const navigateExperience = () => {
    experienceRef.current?.scrollIntoView({behavior: 'smooth'});
  };
  const navigateContactMe = () => {
    contactRef.current?.scrollIntoView({behavior: 'smooth'});
  };
  const navigate = useNavigate();
  const  navigateBlog = () => {
    navigate("/blog");
  };
  const openGit = () => {
    window.open("https://www.github.com/raviprakash11/",'_blank', 'noopener,noreferrer');
  }
  const openLinkedin = () => {
    window.open("https://www.linkedin.com/in/raviprakash11/",'_blank', 'noopener,noreferrer');
  }
  const openTwitter = () => {
    window.open("https://www.twitter.com/raviprakashz/",'_blank', 'noopener,noreferrer');
  }
  const openYoutube = () => {
    window.open("https://www.youtube.com/@strangedesk",'_blank', 'noopener,noreferrer');
  }
  const openInsta = () => {
    window.open("https://www.instagram.com/__raviprakash/",'_blank', 'noopener,noreferrer');
  }
  const openEmail = () => {
    window.open(`https://outlook.office.com/mail/0/deeplink/compose?subject=&body=&to="ravi.p.prakash@hotmail.com`,'_blank', 'noopener,noreferrer');
  }
  const [loading, setLoading] = useState(true);

  useLayoutEffect(() => {
    setLoading(true);
    const timer = setTimeout(() => {
      setLoading(false);
    }, 4000);
    return () => clearTimeout(timer);
  }, []);
  return loading ? <LoadingBar/> : (
    <div
      style={{
        width: "100%",
        display: "flex",
        justifyContent: "center",
        flexDirection: "column",
      }}
      className={lightTurnOn ? "night-light" : "day-light"}
    >
      <div
        style={{
          width: "100%",
          display: "flex",
          justifyContent: "space-between",
        }}
      >
        <div
          style={{ width: "50px", display: "flex", justifyContent: "center" }}
        >
          <div
            style={{
              width: "18px",
              display: "flex",
              justifyContent: "center",
              alignItems: "flex-end",
            }}
          >
            <div
              style={{
                marginLeft: "18px",
                width: "4%",
                display: "flex",
                justifyContent: "space-between",
                flexDirection: "column",
                alignItems: "center",
                position: "fixed",
              }}
            >
              <div />
              <div>
                <div>
                  <IconButton onClick={openGit}>
                    <GitHubIcon
                      className={
                        lightTurnOn
                          ? "icon-clr-night icon-hover-effect"
                          : "icon-clr-day icon-hover-effect"
                      }
                    />
                  </IconButton>
                </div>
                <div >
                  <IconButton onClick={openLinkedin}>
                    <LinkedInIcon
                      className={
                        lightTurnOn
                          ? "icon-clr-night icon-hover-effect"
                          : "icon-clr-day icon-hover-effect"
                      }
                    />
                  </IconButton>
                </div>
                <div>
                  <IconButton onClick={openTwitter}>
                    <TwitterIcon
                      className={
                        lightTurnOn
                          ? "icon-clr-night icon-hover-effect"
                          : "icon-clr-day icon-hover-effect"
                      }
                    />
                  </IconButton>
                </div>
                <div>
                  <IconButton onClick={openYoutube}>
                    <YouTubeIcon
                      className={
                        lightTurnOn
                          ? "icon-clr-night icon-hover-effect"
                          : "icon-clr-day icon-hover-effect"
                      }
                    />
                  </IconButton>
                </div>
                <div>
                  <IconButton onClick={openInsta}>
                    <InstagramIcon
                      className={
                        lightTurnOn
                          ? "icon-clr-night icon-hover-effect"
                          : "icon-clr-day icon-hover-effect"
                      }
                    />
                  </IconButton>
                </div>
                <div
                  style={{
                    marginLeft: "18px",
                    marginTop: "2rem",
                    marginBottom: "-2.4rem",
                  }}
                >
                  <Box
                    style={{
                      width: ".2vw",
                      height: "10rem",
                    }}
                    className={lightTurnOn ? "day-light" : "night-light"}
                  ></Box>
                </div>
              </div>
            </div>
          </div>
        </div>
        <section style={{ width: "86%", height: "auto" }}>
          <div>
            <div
              style={{
                display: "flex",
                justifyContent: "space-between",
                marginTop: "6vh",
                marginBottom: "4vh",
              }}
            >
              <div
                style={{
                  display: "flex",
                  flexDirection: "row",
                  gap: "2.6vw",
                  fontSize: "1.0rem",
                  paddingLeft: "3.4vw",
                }}
              >
                <div className="hover-underline-animation">home</div>
                <div className="hover-underline-animation" onClick={navigateAboutMe}>about</div>
                <div className="hover-underline-animation" onClick={navigateExperience}>experience</div>
                <div className="hover-underline-animation" onClick={navigateMyWork}>work</div>
                <div className="hover-underline-animation" onClick={navigateBlog}>blog</div>
              </div>
              <div
                style={{
                  display: "flex",
                  flexDirection: "row",
                  gap: "2.6vw",
                  fontSize: "1.0rem",
                }}
              >
                <div className="hover-underline-animation" onClick={navigateContactMe}>contact</div>
                <div onClick={handleLightOn}>
                  {lightTurnOn === false ? (
                    <BedtimeIcon />
                  ) : (
                    <LightModeIcon className="sun-icon" />
                  )}
                </div>
              </div>
            </div>
          </div>
          <Grid container>
            <Grid item xs={10}>
              <div style={{ paddingTop: "8vh", paddingLeft: "4vw" }}>
                <div style={{ fontSize: "1.2rem" }}>Hi, my name is,</div>
                <div style={{ fontSize: "5.2rem" }}>Ravi Prakash.</div>
                <div style={{ fontSize: "4.8rem" }}>
                  I build things for the web.
                </div>
                <div style={{ width: "55%", fontSize: "1.2rem" }}>
                  I’m a software engineer specializing in building (and
                  occasionally designing) exceptional digital experiences.
                  Currently, I’m focused on building accessible, human-centered
                  products at Novaenergon.
                </div>
              </div>

              <div style={{ paddingTop: "8vh", paddingLeft: "4vw" }}>
                <input
                  placeholder="enter your email"
                  style={{
                    height: "5vh",
                    width: "18vw",
                    borderRadius: "10px 0px 0px 10px",
                  }}
                />
                <button
                  style={{
                    height: "6vh",
                    width: "8vw",
                    borderRadius: "0px 10px 10px 0px",
                  }}
                >
                  connect me
                </button>
              </div>
            </Grid>
            <Grid
              item
              xs={2}
              style={{ height: "80vh" }}
              className={lightTurnOn ? "light-background" : "night-background"}
            />
          </Grid>
        </section>
        <div
          style={{ width: "50px", display: "flex", justifyContent: "center" }}
        >
          <div
            style={{
              width: "18px",
              display: "flex",
              justifyContent: "center",
              alignItems: "flex-end",
            }}
          >
            <div
              style={{
                // width: "6%",
                display: "flex",
                justifyContent: "space-between",
                flexDirection: "column",
                alignItems: "center",
                position: "fixed",
              }}
            >
              <div />
              <div>
                <div
                  style={{ writingMode: "vertical-rl" }}
                  className="email-main"
                  onClick={openEmail}
                >
                  ravi.p.prakash@hotmail.com
                </div>
                <div
                  style={{
                    marginLeft: "6px",
                    marginTop: "2rem",
                    marginBottom: "-2.4rem",
                  }}
                >
                  <Box
                    style={{
                      width: ".2vw",
                      height: "10rem",
                    }}
                    className={lightTurnOn ? "day-light" : "night-light"}
                  ></Box>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        style={{
          paddingTop: "2rem",
          height: "auto",
          width: "100%",
          display: "flex",
          justifyContent: "center",
        }}
        ref={aboutMeRef}
      >
        <AboutMePage />
      </div>
      <div
        style={{
          paddingTop: "2rem",
          height: "auto",
          width: "100%",
          display: "flex",
          justifyContent: "center",
        }}
        ref={experienceRef}
      >
        <ExperiencesPage />

      </div>
      <div
        style={{
          paddingTop: "2rem",
          height: "auto",
          width: "100%",
          display: "flex",
          justifyContent: "center",
        }}
        ref={myWorkRef}
      >
        <MyWorkPage />
      </div>
      <div
        style={{
          paddingTop: "2rem",
          height: "auto",
          width: "100%",
          display: "flex",
          justifyContent: "center",
        }}
      >
        <FeedbackPage/>

      </div>
      <div
        style={{
          paddingTop: "2rem",
          height: "auto",
          width: "100%",
          display: "flex",
          justifyContent: "center",
        }}
        ref={contactRef}
      >
        <ContactMePage/>
      </div>
      {showButton && (
        <button onClick={scrollToTop} className="back-to-top">
          ▲
        </button>
      )}
      <div
        style={{
          paddingTop: "2rem",
          height: "auto",
          width: "100%",
          display: "flex",
          justifyContent: "center",
        }}
      >
        <FooterMainPage/>
      </div>
    </div>
  );
}
